        @click="showDialog('consult', 'all')"></el-button>
      <el-button size="small" @click="showDialog('records')">
        
      </el-button>
      <template v-if="crmRoleId === 5">
        <router-link class="el-button--small" tag="el-button" to="/pushmsg/apppushmsg"></router-link>
        <router-link class="el-button--small" tag="el-button" to="/pushmsg/pushmsglist"></router-link>
      </template>
      <Alert type="warning" show-icon style="display: inline-block; marginLeft: 10px">
        
