<script>
import CustomerMixin from '../mixin/customerDetail'
export default {
  mixins: [CustomerMixin],
  props: {
    tableData: {
      type: Array,
      default: () => {
        return []
      }
    },
    loading: {
      type: Boolean,
      default: () => {
        return false
      }
    },
    flagShow: {
      type: Boolean,
      default: () => {
        return false
      }
    },
    crmRoleId: {
      type: Number,
      default: () => {
        return -1
      }
    },
    roleName: {
      type: String,
      default: () => {
        return ''
      }
    }
  },
  data: function () {
    return {
      multipleSelection: []
    }
  },
  methods: {
    handleSelectionChange (val) {
      this.multipleSelection = val
    },
    showDialog (identity, trackType) {
      this.$emit('search')
      // 
      if (trackType) {
        let trackId = null
        let button_name = ''
        if (trackType === 'part') {
          trackId = '15-5128'
          button_name = ''
        } else if (trackType === 'all') {
          trackId = '15-5129'
          button_name = ''
        }

        XesAnalytics.track(trackId, {
          button_name
        })
      }
      this.$store.commit('resource/setIdentity', identity)
      this.$store.commit(
        'resource/setmultipleSelection',
        this.multipleSelection.map(item => {
          return item.uid
        })
      )
    },
    sortChange (sortProps) {
      this.$emit('sortchange', sortProps)
    }
  }
}
</script>

