<template>
  <!--  -->
  <div>
    <!-- <h3 style="margin: 16px 0"></h3> -->
    <div v-if="applyChangedRecord.length">
      <Divider />
      <h3 style="margin: 16px 0" ></h3>
      <Scroll height="200">
        <Row style="padding-left: 24px;" v-for="(item, index) in applyChangedRecord" :key="index">
          <Col>: {{ applyStatusMap[item.currentApplyStatus] }}</Col>
          <Col>: {{ item.remark }}</Col>
          <Col span="8">: {{ item.modifyEmpName }}</Col>
          <Col span="16">: {{ item.createTime }}</Col>
          <Col span="24"><Divider /></Col>
        </Row>
      </Scroll>
    </div>
    <Divider />
    <h3 style="marginBottom: 16px"></h3>
    <Form :label-width="100">
      <FormItem label="" required>
        <Cascader
          size="small"
          style="width: 200px;display: inline-block;"
          :data="cascaderList"
          v-model="cascaderValue">
        </Cascader>
      </FormItem>
      <FormItem label="" required v-if="nextTimeShow">
        <DatePicker
          v-model="formData.nextTime"
          @on-change="timeChange"
          :options="options"
          type="datetime"
          placement="top"
          size="small"
          format="yyyy-MM-dd HH:mm"
          placeholder=""
          style="width: 200px">
        </DatePicker>
      </FormItem>
      <FormItem label="" required>
        <Input v-model="formData.remark" placeholder="" type="textarea" :minlength="5" style="width: 80%;" />
      </FormItem>
    </Form>
    <Row type="flex" justify="center" :gutter="20">
      <Col><Button type="primary" @click="submit"></Button></Col>
      <!-- <Col><Button type="default" @click="cancel"></Button></Col> -->
    </Row>
  </div>
</template>

<script>
import { modifyApplyStatus } from '@/api/appointmentApply'
export default {
  name: 'changeApplyStatus',
  props: {
    applyChangedRecord: {
      type: Array
    },
    applyInfo: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data () {
    return {
      options: {
        disabledDate (date) {
          return date && date.valueOf() < Date.now() - 86400000
        }
      },
      nextTime: '',
      nextTimeShow: false, // 
      formData: {
        remark: '', // 
        applyType: 1, // 1: 
        preApplyStatus: 0, // 
        nextFollowUpTime: '' // 
      },
      applyStatusMap: {
        0: '',
        1: '',
        2: '',
        3: ''
      },
      applySecondStatusMap: {
        1: '',
        2: '',
        3: '',
        4: '',
        5: '',
        6: ''
      },
      cascaderValue: [], // 
      // 
      cascaderList: [
        {
          value: 3,
          label: '',
          children: [
            {
              value: 1,
              label: ''
            },
            {
              value: 2,
              label: ''
            }
          ]
        },
        {
          value: 2,
          label: '',
          children: [
            {
              value: 3,
              label: ''
            },
            {
              value: 4,
              label: ''
            },
            {
              value: 5,
              label: ''
            },
            {
              value: 6,
              label: '',
              children: [
                {
                  value: 7,
                  label: ''
                },
                {
                  value: 8,
                  label: ''
                }
              ]
            }
          ]
        }
      ]
    }
  },
  watch: {
    cascaderValue (val) {
      if (val && val.length && val[0] === 3) { // 
        this.nextTimeShow = true
      } else {
        this.nextTimeShow = false
        this.nextTime = ''
        this.formData.nextFollowUpTime = ''
      }
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.formData.preApplyStatus = this.applyInfo.applyStatus
      this.formData.taskIds = this.applyInfo.taskIds

      this.cascaderValue = [
        this.applyChangedRecord.applyStatus,
        this.applyChangedRecord.applySecondStatus,
        this.applyChangedRecord.applyThirdStatus
      ]
      this.formData.applyId = this.applyInfo.id
    })
  },
  methods: {
    timeChange (time) {
      this.formData.nextFollowUpTime = time
    },
    // cancel () {
    //   this.cascaderValue[0] = ''
    //   this.formData.remark = ''
    // },
    async submit () {
      if (!this.cascaderValue[0]) {
        return this.$Message.warning('')
      }
      if (!this.formData.remark.trim()) {
        return this.$Message.warning('')
      }

      const currentApplyStatus = this.cascaderValue[0] // 
      const applySecondStatus = this.cascaderValue[1] || null // 
      const applyThirdStatus = this.cascaderValue[2] || null // 
      if (currentApplyStatus === 3 && !this.formData.nextFollowUpTime) { // 
        return this.$Message.warning('')
      }
      const params = {
        ...this.formData,
        currentApplyStatus,
        applySecondStatus,
        applyThirdStatus
      }

      try {
        await modifyApplyStatus(params)
        this.$Message.success({ content: `` })
        this.$emit('success')
      } catch (error) {
        this.$Message.error({
          content: `: ${error.message}`,
          duration: 5
        })
      }
    }
  }
}
</script>

<style lang="less" scoped>
/deep/ .ivu-col {
  line-height: 2;
}
</style>
