            <p v-if="item.gradeName" >{{item.gradeName}}</p>
            <p v-if="item.subjectNames" >{{item.subjectNames}}</p>
            <p v-if="item.context" >{{item.context}}</p>
            <Divider  v-if="appClassConsultContent.length-1 !== index"/>
          </div>
