</template>

<script>
import NewDataCard from './new-data-card'
import ResourceCard from './resource-card'
import HScroll from './h-srcoll'
import TaskTypeCard from './task-type-card'
import TaskTable from './task-table'
import RankCard from './rank-card'
import { mapState } from 'vuex'
import { taskSummary, topAdvisorType } from '@/api/advisor'
import { getConsultTargetPank, getTargetPank } from '@/api/targetTask'
import { getDeptByAreaCode, resourceWarning } from '@/api/crm'
import TaskMaintainMixin from './task-maintain-mixin'
import MaintainRecordAdd from '@/view/apply/stuArchives/maintainRecordAdd.vue'
import FllowDiagnosisRecord from '@/view/operative/scatteredDiagnosisMgt/fllowDiagnosisRecord'
import FollowRecord from '_c/appointment/followRecord.vue'
import AuditionFollowRecord from '_c/appointment/auditionFollowRecord.vue'
import { XesAnalyticsTrack } from '@/libs/tal-sensors'
import ClassTable from '@/components/mytask/consultTask/classTable'
export default {
  name: 'advisorWorkbench',
  mixins: [TaskMaintainMixin],
  components: {
    NewDataCard,
    ResourceCard,
    HScroll,
    TaskTypeCard,
    TaskTable,
    RankCard,
    MaintainRecordAdd,
    FllowDiagnosisRecord,
    FollowRecord,
    ClassTable,
    AuditionFollowRecord
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.roleRelativeData.userInfo,
      roleId: (state) => state.roleRelativeData.userInfo.crmRoleId,
      cityCode: (state) => state.roleRelativeData.userInfo.cityCode
    })
  },
  watch: {
    cityCode () {
      this.initView()
    }
  },
  data () {
    return {
      waitTaskTypeLoading: false,
      recordTaskTypeLoading: false,
      tabName: 'taskTable1',
      secondHScrollInitSuccess: false, // hscroll
      formData: {
        month: null,
        serviceCenterId: ''
      },
      monthList: [], // 
      serviceCenterList: [], // 
      waitTaskTypeList: [],
      activeWaitTaskType: null, // 
      recordTaskTypeList: [],
      activeRecordTaskType: null, // 
      advisorNewData: {}, // 
      managerNewData: [], // 
      resourceWarningData: {
        regNotAddWechatList: [], // 
        regNotTransferList: [], // 
        productsNotTransformList: [], // 
        customerLevelList: [] // 
      }, // 
      regNotAddWechatTips: `<p style="display: flex; flex-direction: column">
                              <span></span>
                              <span>500500</span>
                            </p> <p style="display: flex; flex-direction: column">
                              <b></b>
                              <span></span>
                            </p> <p style="display: flex; flex-direction: column">
                              <b>7</b>
                              <span>7</span>
                            </p> <p style="display: flex; flex-direction: column">
                              <b>14</b>
                              <span>14</span>
                            </p>`,
      regNotTransferTips: `<p style="display: flex; flex-direction: column">
                              <span></span>
                              <span>500500</span>
                            </p> <p style="display: flex; flex-direction: column">
                              <b>7</b>
                              <span>7</span>
                            </p> <p style="display: flex; flex-direction: column">
                              <b>14</b>
                              <span>14</span>
                            </p> <p style="display: flex; flex-direction: column">
                              <b>30</b>
                              <span>30</span>
                            </p>`,
      productsNotTransformTips: `<p style="display: flex; flex-direction: column">
                              <span>30</span>
                              <span>500500</span>
                            </p> <p style="display: flex; flex-direction: column">
                              <b></b>
                              <span></span>
                            </p> <p style="display: flex; flex-direction: column">
                              <b></b>
                              <span></span>
                            </p>`,
      customerLevelTips: `<p style="display: flex; flex-direction: column">
                              <span>30</span>
                              <span>500500</span>
                            </p>`
    }
  },
  mounted () {
    // console.log('USER-INFO' + JSON.stringify(this.userInfo))
    this.initView()
  },
  methods: {
    // 
    initView () {
      this.$nextTick(async () => {
        if (this.cityCode && this.roleId) {
          // 
          if (this.roleId === 2) {
            this.queryDeptByAreaCode()
          }
          //  / 
          if (this.roleId === 4) {
            this.getTargetPank()
          }
          // 
          if (this.roleId === 5) {
            this.inintMonth()
            this.getConsultTargetPank()
          }
          this.taskSummary(1)
          this.taskSummary(3)
          this.$refs.taskTable1.getTableData()
          this.$refs.taskTable2.getTableData()
          this.resourceWarning()
        }
      })
    },
    //  
    handleRefresh () {
      this.taskSummary(1)
      this.taskSummary(3)
      this.$refs.taskTable1.getTableData()
      this.$refs.taskTable2.getTableData()
    },
    handleTabClick (name) {
      //  
      let taskName, code
      if (name === 'taskTable1') {
        taskName = ''
      } else {
        taskName = ''
      }
      XesAnalyticsTrack('15-5004', {
        tab_name: taskName,
        code: code
      })
      this.secondHScrollInitSuccess = true
      if (this.$refs[name + 'Scroll']) {
        this.$refs[name + 'Scroll'].refresh()
      }
    },
    goTargetMgt () {
      this.$router.push({ name: 'targetMgt' })
    },
    // 
    handleServiceCenterChange (serviceId) {
      this.getTargetPank(serviceId)
    },
    // Count
    handleResourceCountClick (type, typeName, data) {
      this.$router.push({
        name: `resourceList2ForMulti`,
        params: {
          id: new Date().getTime()
        },
        query: { type, typeName, ...data }
      })

      XesAnalyticsTrack('15-5009', {
        cardValue_id: typeName,
        card_name: data.name,
        card_num: data.count
      })
    },
    // 
    async resourceWarning () {
      try {
        const params = {
          crmRoleId: this.userInfo.crmRoleId,
          crmDeptId: this.userInfo.crmDeptId,
          cityCode: this.userInfo.cityCode,
          baseDataId: this.userInfo.baseDataId
        }
        const data = await resourceWarning(params)

        this.resourceWarningData = data
      } catch (error) {
        this.$Message.error({
          content: `: ${error.message}`,
          duration: 5
        })
      }
    },
    // 
    handleMonthChange () {
      // 
      XesAnalyticsTrack('15-5002', { month: this.formData.month })
      this.getConsultTargetPank()
    },
    // 
    inintMonth () {
      const month1 = new Date().getMonth() + 1
      let month0, month2, month3
      month0 = month1 - 1
      month2 = month1 + 1
      if (month0 === 0) month0 = 12
      if (month2 === 13) month2 = 1
      month3 = month2 + 1
      if (month3 === 13) month3 = 1
      this.monthList = [month0, month1, month2, month3]
      this.formData.month = month1
    },
    // 
    async getTargetPank (serviceId) {
      try {
        const params = {
          cityCode: this.cityCode,
          serviceId: serviceId || this.userInfo.crmDeptId
        }
        const data = await getTargetPank(params)
        this.managerNewData = data
      } catch (error) {
        this.$Message.error({
          content: `: ${error.message}`,
          duration: 5
        })
      }
    },
    // 
    async getConsultTargetPank () {
      try {
        const year = new Date().getFullYear()
        const params = {
          cityCode: this.cityCode,
          month: this.formData.month,
          year
        }
        const data = await getConsultTargetPank(params)
        this.advisorNewData = Object.freeze(data)
      } catch (error) {
        this.$Message.error({
          content: `: ${error.message}`,
          duration: 5
        })
      }
    },
    // 
    queryDeptByAreaCode () {
      getDeptByAreaCode({
        areaCode: this.cityCode,
        isActive: 1 // 1  0 
      })
        .then((data) => {
          this.serviceCenterList = Object.freeze(data)
          if (this.serviceCenterList && this.serviceCenterList[0]) {
            this.formData.serviceCenterId = this.serviceCenterList[0].id
            this.getTargetPank(this.formData.serviceCenterId)
          }
        })
        .catch((err) => {
          this.$message.error('' + err.message)
        })
    },
    // 
    async taskSummary (type) {
      try {
        if (type === 1) {
          this.waitTaskTypeLoading = true
        } else if (type === 3) {
          this.recordTaskTypeLoading = true
        }
        const params = {
          cityCode: this.cityCode,
          crmDeptId: this.userInfo.crmDeptId, // id
          crmRoleId: this.roleId, // id
          type // 12: 3
        }

        const data = await taskSummary(params)

        data.forEach((item, index) => {
          if (index === 0) item.checked = true
          else item.checked = false
        })
        if (type === 1) {
          this.waitTaskTypeList = data
        } else if (type === 3) {
          this.recordTaskTypeList = data
        }
      } catch (error) {
        this.$Message.error({
          content: `: ${error.message}`,
          duration: 5
        })
      }

      if (type === 1) {
        this.waitTaskTypeLoading = false
      } else if (type === 3) {
        this.recordTaskTypeLoading = false
      }
    },
    async topAdvisorType (params, type) {
      try {
        await topAdvisorType(params)
        // 
        await this.taskSummary(type)
        let active = null
        if (type === 3) {
          active = this.activeRecordTaskType
        } else if (type === 1) {
          active = this.activeWaitTaskType
        }

        if (active) {
          console.log('active', active)
          this.handleTaskClick(type, active)
        }
      } catch (error) {
        this.$Message.error({
          content: `: ${error.message}`,
          duration: 5
        })
      }
    },
    // 
    handleTop (type, data) {
      // 
      XesAnalyticsTrack('15-5003', {
        task_name: data.taskName
      })
      this.topAdvisorType({ ...data, isTop: 1 }, type)
    },
    // 
    handleCancelTop (type, data) {
      this.topAdvisorType({ ...data, isTop: 0 }, type)
    },
    // 
    handleTaskClick (type, obj) {
      // 
      XesAnalyticsTrack('15-5005', {
        task_name: type === 1 ? '' : '',
        filter_type: obj.taskName
      })
      // 
      if (type === 1) {
        this.activeWaitTaskType = obj
        this.waitTaskTypeList.forEach((item) => {
          item.checked = false
          if (item.taskType === obj.taskType) {
            item.checked = true
          }
        })
        this.$refs.taskTable1.getTableData(obj.taskType)
      } else if (type === 3) {
        this.activeRecordTaskType = obj
        // 
        this.recordTaskTypeList.forEach((item) => {
          item.checked = false
          if (item.taskType === obj.taskType) {
            item.checked = true
          }
        })
        this.$refs.taskTable2.getTableData(obj.taskType)
      }
    }
  }
}
</script>

