import { queryDiagnosisRecordIdsByPyUid } from '@/api/diagnosis'
import { queryStudentDiagnosis, queryStudentTaskActivity, queryAppClassConsult } from '@/api/crm'
import { getStudentDetail } from '@/api/student'
import { getAppointmentIdByPyUid } from '@/api/appointment'
import { getAppointmentApplyAndRecordRList } from '@/api/appointmentApply'
import { getStudentNeedFollowTask, addFollowRecord } from '@/api/crmStudent'
import { XesAnalyticsTrack } from '@/libs/tal-sensors'
import Cookie from 'js-cookie'
export default {
  data () {
    return {
      showBackupRecordModal: false, // 
      showNoPaymentModal: false, // 
      diagnosisModal: false, // 
      showDiagnosisFllowModal: false, // 
      showAuditionFollowModal: false, // 
      showAuditionApplyFollowModal: false, // 
      keyActionModal: false, // 
      maintainRecordAddModal: false, // 
      stuDetail: {},
      followDiagnosisArr: [], // id
      followAuditionArr: [], // id
      followApplyArr: [], // id
      activeTabId: '', // tabname
      diagnosisData: [], // 
      diagnosisColumns: [
        {
          type: 'index',
          width: 30,
          align: 'center'
        },
        {
          title: '',
          key: 'cityName',
          align: 'center'
        },
        {
          title: '',
          key: 'gradeName',
          align: 'center'
        },
        {
          title: '',
          key: 'subjectName',
          align: 'center'
        },
        {
          title: '',
          key: 'termName',
          align: 'center'
        },
        {
          title: '',
          key: 'stuCode',
          align: 'center',
          width: 100
        },
        {
          title: '',
          key: 'stuName',
          align: 'center'
        },
        {
          title: '',
          key: 'score',
          align: 'center'
        },
        {
          title: '',
          key: 'levelDegreeName',
          align: 'center'
        },
        {
          title: '',
          key: 'levelName',
          align: 'center'
        },
        {
          title: 'URL',
          slot: 'reportUrl',
          align: 'center',
          width: 130
        },
        {
          title: '',
          key: 'sourceTypeName',
          align: 'center'
        },
        {
          title: '',
          key: 'effectiveClaLevelTime',
          align: 'center'
        },
        {
          title: '',
          slot: 'isValid',
          align: 'center'
        },
        {
          title: '',
          slot: 'forRegist',
          align: 'center'
        },
        {
          title: '',
          key: 'answerDateTime',
          align: 'center',
          width: 130
        }
      ],
      keyActionData: [], // 
      cubeCols: [
        { title: '', align: 'center', key: 'activityName' },
        { title: '', align: 'center', key: 'type' },
        { title: 'URL', align: 'center', slot: 'url' }
      ],
      maintainRecordAddData: {}, // 
      appClassConsultContent: [],
      isShowAppConsultModal: false // APP 
    }
  },
  methods: {
    // 
    refreshFullData () {
      setTimeout(() => {
        this.taskSummary(1)
        this.taskSummary(3)
        this.$refs.taskTable1.getTableData()
        this.$refs.taskTable2.getTableData()
      }, 1500)
    },
    // 
    handleAction ({ type, row }) {
      // console.log(row)
      let editName = ''
      switch (type) {
        case 1:
          // console.log('')
          editName = ''
          this.followDiagnosisHandle(row)
          break
        case 2:
          // console.log('')
          editName = ''
          this.followAuditionHandle(row)
          break
        case 3:
          // console.log('')
          editName = ''
          this.diagnosisHandle(row.taskList)
          break
        case 4:
          // console.log('')
          editName = ''
          this.classInfoHandle(row)
          break
        case 5:
          // console.log('')
          editName = ''
          this.backupRecordHandle(row)
          break
        case 6:
          // console.log('')
          editName = ''
          this.showKeyAction(row)
          break
        case 7:
          //  
          XesAnalyticsTrack('15-5006', {
            task_name: this.activeRecordTaskType ? this.activeRecordTaskType.taskName : ''
          })
          this.maintainRecordAdd(row)
          break
        case 8:
          // console.log('')
          editName = ''
          this.auditionApplyHandle(row)
          break
        case 9:
          // APP
          this.appClassConsult(row)
          break
      }

      if (editName) {
        // 
        XesAnalyticsTrack('15-5007', {
          task_name: editName
        })
      }
    },
    addSuccess (params) { // 
      let obj = params
      obj.identityId = ''
      obj.pyUid = this.stuDetail.pyUid
      addFollowRecord(obj).then(() => {
        this.maintainRecordAddModal = false
        this.$Message.success({
          content: '',
          duration: 5
        })
        this.$refs.addrecord.setValue()
        this.refreshFullData()
      }).catch(err => {
        this.$Message.error({
          content: '' + err.message,
          duration: 5
        })
      })
    },
    maintainRecordAdd (row) {
      this.stuDetail = { ...row }
      this.maintainRecordAddModal = true
      getStudentNeedFollowTask({
        studentUid: row.pyUid
      }).then(data => {
        this.maintainRecordAddData = data
      }).catch(err => {
        this.$Message.error({
          content: '' + err.message,
          duration: 5
        })
      })
    },
    // 
    showKeyAction (row) {
      this.stuDetail.cityCode = row.areaCode
      this.stuDetail.studentId = row.studentId

      queryStudentTaskActivity({
        studentId: row.studentId,
        pyUid: row.pyUid,
        uid: row.pyUid,
        mode: 4,
        cityCode: row.areaCode
      }).then(data => {
        this.keyActionData = data
        this.keyActionModal = true
      }).catch(err => {
        this.$Message.error('' + err.message)
      })
    },
    //   
    handleFollowAuditionRecordSuccess () {
      this.refreshFullData()
    },
    //  tabs
    handleFollowAuditionTabsClick (name) {
      this.followAuditionArr.forEach((item) => {
        if (item.id === name) item.init = true
      })
    },
    //   
    handleFollowDiagnosisRecordSuccess () {
      this.refreshFullData()
    },
    //  tabs
    handleFollowDiagnosisTabsClick (name) {
      this.followDiagnosisArr.forEach((item) => {
        if (item.id === name) item.init = true
      })
    },
    //  
    async followDiagnosisHandle (row) {
      this.stuDetail = {
        realName: row.stuRealName,
        gradeName: row.stuGradeName,
        card: row.stuLoginName,
        cityCode: row.areaCode,
        areaCode: row.areaCode,
        uid: row.pyUid
      }
      try {
        // idid
        const res = await queryDiagnosisRecordIdsByPyUid({ pyUid: row.pyUid })
        res.forEach((item, index) => {
          if (index === 0) {
            this.activeTabId = item.id
            item.init = true
          } else item.init = false
        })
        this.followDiagnosisArr = res
        this.showDiagnosisFllowModal = true
      } catch (error) {
        this.$Message.error({ content: error.message, duration: 5 })
      }
    },
    // APP
    async appClassConsult (row) {
      let taskId = []
      if (row.taskList && row.taskList.length > 0) {
        row.taskList.forEach(item => {
          if (item.taskType === 40) {
            taskId.push(item.taskId)
          }
        })
      }
      taskId = taskId.length > 0 && taskId.join(',')
      try {
        const reqParams = {
          taskId
        }
        const res = await queryAppClassConsult(reqParams)
        this.appClassConsultContent = res || []
        this.isShowAppConsultModal = true
      } catch (error) {
        this.isShowAppConsultModal = false
        this.$Message.error({ content: error.message, duration: 5 })
      }
    },
    // 
    diagnosisHandle (list) {
      try {
        let taskList = []
        list.forEach((obj) => {
          taskList.push(obj.taskId)
        })
        this.diagnosisData = []
        this.$nextTick(async () => {
          const rlt = await queryStudentDiagnosis(taskList)
          this.diagnosisData = Object.freeze(rlt)
          this.diagnosisModal = true
        })
      } catch (err) {
        this.$Message.error({
          content: '' + err.message
        })
      }
    },
    // 
    classInfoHandle (stu) {
      const params = {
        cityCode: stu.areaCode,
        uid: stu.pyUid
      }
      getStudentDetail(params)
        .then((res) => {
          this.stuDetail = {
            cityCode: stu.cityCode,
            studentId: res.studentId,
            bindingCode: res.bindingCode
          }
          this.showNoPaymentModal = true
        })
        .catch((err) => {
          this.$Message.error({
            content: ': ' + err.message,
            duration: 5
          })
        })
    },
    // 
    backupRecordHandle (stu) {
      const params = {
        cityCode: stu.areaCode,
        uid: stu.pyUid
      }
      getStudentDetail(params)
        .then((res) => {
          this.stuDetail = {
            cityCode: stu.cityCode,
            studentId: res.studentId,
            bindingCode: res.bindingCode
          }
          this.showBackupRecordModal = true
        })
        .catch((err) => {
          this.$Message.error({
            content: ': ' + err.message,
            duration: 5
          })
        })
    },
    //  
    async followAuditionHandle (row) {
      this.stuDetail = {
        realName: row.stuRealName,
        gradeName: row.stuGradeName,
        card: row.stuLoginName,
        cityCode: row.areaCode,
        areaCode: row.areaCode,
        uid: row.pyUid
      }
      try {
        // idid
        // operationType 1: 
        let res = await getAppointmentIdByPyUid({
          pyUid: row.pyUid,
          operationType: 1
        })
        res = res.map((item, index) => {
          if (index === 0) {
            this.activeTabId = item.id
            return Object.assign(item, { init: true })
          } else {
            return Object.assign(item, { init: false })
          }
        })
        if (!res || res.length === 0) {
          this.$Message.info({ content: '' })
          return
        }
        this.followAuditionArr = res
        this.showAuditionFollowModal = true
      } catch (error) {
        this.$Message.error({ content: error.message, duration: 5 })
      }
    },
    //  tabs
    handleFollowApplyTabsClick (name) {
      this.followApplyArr.forEach((item) => {
        if (item.id === name) item.init = true
      })
    },
    //  
    async auditionApplyHandle (row) {
      try {
        let res = await getAppointmentApplyAndRecordRList({
          pyUid: row.pyUid,
          empNo: JSON.parse(Cookie.get('columbusUserInfo')).id
        })
        res = res.appointmentList.map((item, index) => {
          if (index === 0) {
            this.activeTabId = item.id
            return Object.assign(item, { init: true })
          } else {
            return Object.assign(item, { init: false })
          }
        })
        if (!res || res.length === 0) {
          this.$Message.info({ content: '' })
          return
        }
        this.followApplyArr = res
        this.showAuditionApplyFollowModal = true
      } catch (error) {
        this.$Message.error({ content: error.message, duration: 5 })
      }
    }
  }
}
