<template>
  <div>
    <Row type="flex">
      <Col span="8"><span>: {{ appointmentList.stuName || '--' }}</span></Col>
      <Col span="8"><span>: {{ appointmentList.gradeName || '--' }}</span></Col>
      <Col span="8"><span>: {{ appointmentList.stuCode || '--' }}</span></Col>
    </Row>
    <br>
    <Row type="flex">
      <Col span="8"><span>: {{ appointmentList.createTime || '--' }}</span></Col>
      <Col span="8"><span>: {{ appointmentList.maintainerName || '--' }}</span></Col>
      <Col span="8"><span>: {{ appointmentList.referrerStuName || '--' }}</span></Col>
    </Row>
    <br>
    <Row type="flex">
      <Col span="8"><span>: {{ appointmentList.intendedServiceCenter || '--' }}</span></Col>
      <Col span="8"><span>: {{ appointmentList.year || '--' }}</span></Col>
      <Col span="8"><span>: {{ appointmentList.gradeName || '--' }}</span></Col>
    </Row>
    <br>
    <Row type="flex">
      <Col span="8"><span>: {{ appointmentList.subject || '--' }}</span></Col>
      <Col span="8"><span>: {{ appointmentList.classType || '--' }}</span></Col>
      <Col span="8"><span>: {{ appointmentList.subTime || '--' }}</span></Col>
    </Row>
    <br>
    <!--  -->
    <ChangeApplyStatus v-if="changeApplyStatusModal.show"
      :applyChangedRecord="appointmentList.recordList"
      :applyInfo="appointmentList"
      @success="$emit('success')"
    />
  </div>
</template>

<script>
import { queryFollowDetail } from '@/api/appointment'
import TaskMaintainMixin from '@/view/resource/advisor-workbench/task-maintain-mixin'
import ChangeApplyStatus from './change-apply-status'
export default {
  name: 'AuditionFollowRecord', // 
  mixins: [TaskMaintainMixin],
  components: {
    ChangeApplyStatus
  },
  props: {
    // cityCode / areaId / uid / realName / stuCard
    studentDetail: {
      type: Object,
      default: () => {
        return {}
      }
    },
    operationType: {
      //  {1: }
      type: Number,
      default: null
    },
    appointmentList: {
      type: Object,
      default: () => {
        return {}
      }
    },
    applyId: {
      type: String,
      default: ''
    },
    preApplyStatus: {
      type: Number
    }
  },
  data () {
    return {
      followList: [], // 
      taskResp: [], // 
      changeApplyStatusModal: {
        show: true
      },
      attendStatus: {
        // 1  2 0
        0: '',
        1: '',
        2: ''
      },
      classTypeMap: {
        1: '',
        2: '',
        4: ''
      },
      appointmentApply: null, // 
      loaded: false,
      applyStatusMap: { // 
        0: '',
        1: '',
        2: '',
        3: ''
      },
      applySecondStatusMap: { // 
        1: '',
        2: '',
        3: '',
        4: '',
        5: '',
        6: ''
      }
    }
  },
  methods: {
    refresh () {
      this.queryFollowDetail()
    },
    queryFollowDetail () {
      const params = {
        appointmentId: this.appointmentId,
        cityCode: this.studentDetail.cityCode,
        operationType: this.operationType,
        appointmentApplyId: this.appointmentApplyId
      }
      queryFollowDetail(params)
        .then((res) => {
          this.followList = Object.freeze(res.followList) || []
          this.appointmentResp = Object.freeze(res.appointmentResp) || {}
          this.taskResp = Object.freeze(res.taskResp) || []
          this.appointmentApply = Object.freeze(res.appointmentApply) || null
          this.loaded = true
        })
        .catch((err) => {
          this.loaded = true
          this.$Message.error({ content: err.message, duration: 5 })
        })
    }
  }
}
</script>

