<template>
  <el-table
    v-loading="loading"
    :data="tableData"
    style="width: 100%">
    <el-table-column
      prop="stuLoginName"
      label=""
      width="200"
      align="center"/>
    <el-table-column
      label=""
      align="center">
      <template slot-scope="scope">
        <el-button
          type="text"
          @click="goCustomerDetail(scope.row)">{{ scope.row.stuName }}</el-button>
      </template>
    </el-table-column>
    <el-table-column
      label=""
      width="200"
      align="center">
      <template slot-scope="scope">
        <TalDecodeText type="phone"
                       :uid="scope.row.pyUid"
                       :encodeText="scope.row.phone"/>
      </template>
    </el-table-column>
    <el-table-column
      prop="isNew"
      label=""
      align="center">
      <template slot-scope="scope">
        {{ scope.row.isNew ? '' : ''}}
      </template>
    </el-table-column>
    <el-table-column
      prop="attendTime"
      label=""
      width="200"
      align="center"/>
    <el-table-column
      label=""
      align="center">
      <template slot-scope="scope">
        <el-button v-if="scope.row.firstGiftReceived" type="primary" size="mini" disabled></el-button>
        <el-button v-else type="primary" size="mini" @click="pickGift(scope.row, 1)"></el-button>
      </template>
    </el-table-column>
    <el-table-column
      label=""
      align="center">
      <template slot-scope="scope">
        <el-button v-if="scope.row.secondGiftReceived" type="primary" size="mini" disabled></el-button>
        <el-button v-else type="primary" size="mini" @click="pickGift(scope.row, 2)"></el-button>
      </template>
    </el-table-column>
    <el-table-column
      label=""
      align="center">
      <template slot-scope="scope">
        <el-button v-if="scope.row.thirdGiftReceived" type="primary" size="mini" disabled></el-button>
        <el-button v-else type="primary" size="mini" @click="pickGift(scope.row, 3)"></el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
import CustomerMixin from '../../mixin/customerDetail'
import { updateGiftReceiveStatus } from '../../../api/crm'
export default {
  mixins: [CustomerMixin],
  name: 'giftTask',
  props: {
    tableData: Array,
    loading: {
      type: Boolean,
      default: function () {
        return false
      }
    }
  },
  methods: {
    pickGift (row, level) {
      if (level === 2 && row.firstGiftReceived === 0) {
        this.$Message.warning({
          content: '',
          duration: 5
        })
        return
      }
      if (level === 3 && row.secondGiftReceived === 0) {
        this.$Message.warning({
          content: '',
          duration: 5
        })
        return
      }
      this.$Modal.confirm({
        title: '',
        content: '',
        onOk: () => {
          updateGiftReceiveStatus({
            id: row.id,
            firstGiftReceived: level === 1 ? 1 : 0,
            secondGiftReceived: level === 2 ? 1 : 0,
            thirdGiftReceived: level === 3 ? 1 : 0
          }).then(data => {
            if (data === 1) {
              this.$Message.success({
                content: '',
                duration: 5
              })
              this.$emit('pickGiftSuccess')
            } else {
              this.$Message.error({
                content: '',
                duration: 5
              })
            }
          }).catch(err => {
            this.$Message.error({
              content: '' + err.message,
              duration: 5
            })
          })
        }
      })
    }
  }
}
</script>

